import React from 'react';
import { Form, Icon, Input, Button } from 'antd';
import CanvasBox from '../canvasBackground/CanvasBg';
import LogoImg from '../../style/imgs/logo.png';
import Vcode from 'react-vcode';
import tools from '../../utils/tools';
import '../../style/login.less';

const FormItem = Form.Item;

class Register extends React.Component {

  // label , input
  formItemLayout = {
    labelCol: { span: 6 },
    wrapperCol: { span: 18 },
  };

  // 
  onVcodeChange(code) {
    const form = this.props.form;
    form.setFieldsValue({
      vcode: code // ''
    });
    this.setState({
      codeValue: code
    });
  }

  handleSubmit = e => {};

  // 
  sendSmsCode = () => {
    // sendSms()
  }

  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <div className="login">
        <div className="login-form">
          {/* canvas */}
          <div className="canvasBox">
            <CanvasBox row={12} col={8} />
          </div>
          <Form onSubmit={this.handleSubmit} style={{ maxWidth: '300px' }} className="formStyle" >
            <div className="login-logo">
              <span className="login-name">
                <img src={LogoImg} alt="logo" />
                <span>Gszs-React-Register</span>
              </span>
            </div>
            <FormItem {...this.formItemLayout} className="userStyle" >
              {getFieldDecorator('phoneNumber', {
                rules: [{ required: true, message: '!' }]
              })(
                <Input
                  prefix={<Icon type="mobile" style={{ fontSize: 13 }} />}
                  placeholder=""
                />
              )}
            </FormItem>
            <FormItem>
              {getFieldDecorator("vcode", {
                rules: [
                  {
                    validator: (rule, value, callback) => {
                      const v = tools.trim(value);
                      if (v) {
                        if (v.length > 4) {
                          callback("4");
                        } else if (
                          v.toLowerCase() !==
                          this.state.codeValue.toLowerCase()
                        ) {
                          callback("");
                        } else {
                          callback();
                        }
                      } else {
                        callback("");
                      }
                    }
                  }
                ]
              })(
                <Input
                  className="inputCode"
                  size="large"
                  id="vcode" // 
                  placeholder=""
                  onPressEnter={() => this.onSubmit()}
                />
              )}
              <Vcode
                height={40}
                width={150}
                onChange={code => this.onVcodeChange(code)}
                className="vcode"
                options={{
                  lines: 16
                }}
              />
            </FormItem>
            <FormItem>
              {
                getFieldDecorator('VerificationCode', {
                  rules: [{ required: true, message: '!' }]
                })(
                  <Input
                    className="inputCode"
                    size="large"
                    placeholder=""
                  />
                )
              }
              <Button 
                height={40}
                width={150}
                className="vcode"
                onClick = {this.sendSmsCode}
              >
                
              </Button>
            </FormItem>
            <FormItem>
              {
                getFieldDecorator('password', {
                  rules: [{ required: true, message: '!' }]
                })(
                  <Input
                    prefix={<Icon type="lock" style={{ fontSize: 13 }} />}
                    placeholder=""
                  />
                )
              }
            </FormItem>
            <FormItem>
              <Button
                type="primary"
                htmlType="submit"
                className="login-form-button"
                style={{ width: '100%' }}
              >
                
              </Button>
            </FormItem>
          </Form>
        </div>
      </div>
    );
  }
}

export default Form.create()(Register);