import React, { Component } from 'react';
import screenfull from 'screenfull';
import avater from '../style/imgs/Qiong.jpeg';
import SiderCustom from './SiderCustom';
import { Menu, Icon, Layout, Popover } from 'antd';
import { withRouter } from 'react-router-dom';
import { PwaInstaller } from './widget';
import { PublicModal } from './publicComponents/_Modal';
import { ModalComponents } from './Common/ModalComponents';
import { SVGICON } from './svg/svgIcon';

const { Header } = Layout;
const SubMenu = Menu.SubMenu;
const MenuItemGroup = Menu.ItemGroup;

class HeaderCustom extends Component {
  state = {
    user: '',
    visible: false,
    modalOpen: false,
    _Modalvisible: false,
  };
  screenFull = () => {
    if (screenfull.enabled) {
      screenfull.request();
    }
  };
  // 
  menuClick = e => {
    if (e.key === 'userSetting') {
      this.setState({
        modalOpen: true,
      });
    }
  };

  // 
  logout = () => {
    this.setState({
      _Modalvisible: true,
    });
  };

  // 
  handleModalOk = () => {
    this.setState({
      _Modalvisible: false,
    });
    this.props.logout((()=>{this.props.history.push('/login')}));
  };

  popoverHide = () => {
    this.setState({
      visible: false,
    });
  };
  handleVisibleChange = visible => {
    this.setState({ visible });
  };
  render() {
    // 
    const modalConfig = {
      title: '',
      content: 'Hello world',
      controlModal: this.state.modalOpen,
      contentType: 'upload',
    };
    // const responsive = {data: {}}, path} = this.props;
    return (
      <React.Fragment>
        <PublicModal modalConfig={modalConfig} />
        {/*  */}
        <ModalComponents
          Modaltitle=""
          Modalvisible={this.state._Modalvisible}
          ModalokText=""
          ModalcancelText=""
          ModalonCancel={() => {
            this.setState({
              _Modalvisible: false,
            });
          }}
          ModalonOk={this.handleModalOk}
          width="700"
        >
          <span
            style={{
              fontSize: '20px',
            }}
          >
            <SVGICON
              style={{
                fontSize: '25px',
                padding: '0 20px',
              }}
              type="icon-jinggaochenghuang"
            />
            ?
          </span>
        </ModalComponents>
        <Header className="custom-theme header">
          {this.props._isMobile
            ? <Popover
                content={
                  // <SiderCustom path={path} popoverHide={this.popoverHide} />
                  <SiderCustom popoverHide={this.popoverHide} />
                }
                trigger="click"
                placement="bottomLeft"
                visible={this.state.visible}
                onVisibleChange={this.handleVisibleChange}
              >
              <Icon type="bars" className="header__trigger custom-trigger" />
            </Popover>
            : <Icon
                className="header__trigger custom-trigger"
                type={this.props.collapsed ? 'menu-unfold' : 'menu-fold'}
                onClick={this.props.toggle}
              />
            }
          <Menu
            mode="horizontal"
            style={{ lineHeight: '64px', float: 'right' }}
            onClick={this.menuClick}
          >
            <Menu.Item key="pwa">
              <PwaInstaller />
            </Menu.Item>
            <Menu.Item key="full" onClick={this.screenFull}>
              <Icon type="arrows-alt" onClick={this.screenFull} />
            </Menu.Item>
            <SubMenu
              title={
                <span className="avatar">
                  <img src={avater} alt="" />
                  <i className="on bottom b-white" />
                </span>
              }
            >
              <MenuItemGroup title="">
                <Menu.Item key="setting:1">
                   - {this.props.user}
                </Menu.Item>
                <Menu.Item key="logout">
                  <span onClick={this.logout}></span>
                </Menu.Item>
              </MenuItemGroup>
            </SubMenu>
          </Menu>
        </Header>
      </React.Fragment>
    );
  }
}
export default withRouter(HeaderCustom)
