import axios from 'axios';
import {get, post} from './tools';
import * as config from './config';
import qs from 'qs';

// BBC
export const getBbcNews = () =>
  get ({
    url: config.NEWS_BBC,
  });

// npm.json
export const npmDependencies = () =>
  axios
    .get ('./npm.json')
    .then (res => res.data)
    .catch (err => console.log (err));

/**  */
export const weibo = () =>
  axios
    .get ('./weibo.json')
    .then (res => res.data)
    .catch (err => console.log (err));

