/**
 * @description 
 * @method {GET}
 * @param {page}  
 * @param {limit} 
 */
export const LOGMANAGER = (page, limit = undefined) => {
  getToken ();
  if (limit !== undefined) {
    return get ({
      url: config._getAllLogManager + `?page=${page}&limit=${limit}`,
    });
  } else {
    return get ({
      url: config._getAllLogManager + `?page=${page}`,
    });
  }
};

