export const G_getAuthId = data => {
	if(data.length === 0){
		return 'id'
	}else{
		var getAuthIdArr = []; // Id
		data.map(item1 => {
			ownedId.map(item2 => {
				if(item1 === item2.itemDesc){
					return getAuthIdArr.push(item2.key)
				}
			})
		})
	}
	return getAuthIdArr;
};
