import { createSelector } from 'reselect';
import {
  judgementGetTable,
  judgementGetTableTotal,
  judgementGetTableById,
  judgementGetAllCityId
} from './commonSelector';

/**
 * @description state
 */
export const getTableDataSelector = createSelector(
  judgementGetTable,
  getTable => getTable
)

/**
 * @description idstate
 */
export const getTableDataByIdSelector = createSelector(
  judgementGetTableById,
  getTableById => getTableById
)

/**
 * @description total
 */
export const getTableTotalSelector = createSelector(
  judgementGetTableTotal,
  getTotal => getTotal
)

/**
 * @description id
 */
export const getAllCityIdSelector = createSelector(
  judgementGetAllCityId,
  (getAllCityId) => getAllCityId
)