import React, {Component} from 'react';
import {Route, Redirect, Switch} from 'react-router-dom';
import DocumentTitle from 'react-document-title';
import AllComponents from '../components';
import routesConfig from './config';
import queryString from 'query-string'; // URL
import nprogress from 'nprogress';
import 'nprogress/nprogress.css'
// import '../style/font/iconfont.css'

/**
 * @description 
 */
export default class CRouter extends Component {
  /**
   * @description 
   */
  requireAuth = (permission, component) => {
    const auth = this.props.auth;
    const rolename = auth.rolename;
    if(!rolename || rolename !== permission ){
      return <Redirect to={'404'} />
    }
    // const {permissions} = auth.data;
    // if (!permissions || !permissions.includes (permission))
    //   return <Redirect to={'404'} />;
    return component;
  };

  /**
   * @description 
   */
  requireLogin = (component, permission) => {
    nprogress.done();
    const auth = this.props.auth;
    if (!auth) { // 
      return <Redirect to={'/login'} />;
    }
    // return permission ? this.requireAuth (permission, component) : component;
    return component;
  };

  render () {

    return (
      <Switch>
        {Object.keys (routesConfig).map (key =>
          routesConfig[key].map (r => {
            const route = r => {
              // 
              const Component = AllComponents[r.component];
              return (
                <Route
                  key={r.route || r.key}
                  exact
                  path={r.route || r.key}
                  render={props => {
                    nprogress.start()
                    const reg = /\?\S*/g;
                    // ?
                    const queryParams = window.location.hash.match (reg);
                    // ?
                    const {params} = props.match;
                    Object.keys (params).forEach (key => {
                      params[key] =
                        params[key] && params[key].replace (reg, '');
                    });
                    props.match.params = {...params};
                    const merge = {
                      ...props,
                      query: queryParams
                        ? queryString.parse (queryParams[0])
                        : {},
                    };
                    // 
                    const wrappedComponent = (
                      <DocumentTitle title={r.title}>
                        <Component {...merge} routerTitle={r.title} />
                      </DocumentTitle>
                    );
                    // 
                    return r.login
                      ? wrappedComponent
                      : this.requireLogin (wrappedComponent, r.auth);
                  }}
                />
              );
            };
            // 
            return r.component ? route (r) : r.subs.map (r => route (r));
          })
        )}
        <Route render={() => <Redirect to="/404" />} />
      </Switch>
    );
  }
}
