import React, { Component } from 'react';
import Routes from './routes';
import DocumentTitle from 'react-document-title'; // title
import SiderCustom from './components/SiderCustom';
import HeaderCustomContainer from './containers/HeaderCustomContainer'
import { Layout, notification, Icon } from 'antd';
import { ThemePicker } from './components/widget';

const { Content, Footer } = Layout;

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      collapsed: false,
      title: '',
    };
    this.roles = JSON.parse(localStorage.getItem('roles'));
    // 
    this.getClientWidth();
    window.onresize = () => {
      this.getClientWidth();
    };
  }

  componentDidMount() {
    const openNotification = () => {
      notification.open({
        message: ``,
        description: (
          <div>
            <span></span>
          </div>
        ),
        icon: <Icon type="smile-circle" style={{ color: 'red' }} />,
        duration: 0,
      });
      // 
      localStorage.setItem('isFirst', JSON.stringify(true));
    };
    const isFirst = JSON.parse(localStorage.getItem('isFirst'));
    !isFirst && openNotification();
  }

  getClientWidth = () => {
    // responsive
    const clientWidth = window.innerWidth;
    clientWidth <= 992 ? this.props.isMobile(true) : this.props.isMobile(false)
  };
  toggle = () => {
    this.setState({
      collapsed: !this.state.collapsed,
    });
  };
  render() {
    const { title } = this.state;
    return (
      <DocumentTitle title={title}>
        <Layout>
          {/*  */}
          {!this.props._isMobile && <SiderCustom collapsed={this.state.collapsed} />}
          <ThemePicker />
          {/*  */}
          <Layout style={{ flexDirection: 'column' }}>
            {/*  */}
            <HeaderCustomContainer
              toggle={this.toggle}
              collapsed={this.state.collapsed}
              // user={this.roles.title || '' }
              user = {this.roles ? this.roles.title : ''} // 
            />
            {/*  */}
            <Content
              style={{ margin: '0 16px', overflow: 'initial', flex: '1 1 0' }}
            >
              <Routes auth={this.roles} />
            </Content>
            {/*  */}
            <Footer style={{ textAlign: 'center' }}>
              
            </Footer>
          </Layout>
        </Layout>
      </DocumentTitle>
    );
  }
}
export default App
