import axios from 'axios';
import {message} from 'antd';

/**
 * @description get
 * @param url       
 * @param msg       
 * @param headers   header
 */
const env = process.env.NODE_ENV; // 
export const get = ({url, msg = '', headers}) =>
  axios
    .get (url, headers)
    .then (res => {
			// token
			if(env && env === 'development' && res.data.status === 500209){
        window.location.href="http://localhost:3006/#/login";
        window.location.reload()
			}else if(env && env === 'production' && res.data.status === 500209){
        window.location.href="http://47.111.23.117:8081/#/login";
			}else{
				return res.data;
			}
    })
    .catch (msg => {
      message.warn (msg);
    });

/**
 * @description post
 * @param url       
 * @param data      
 * @param msg       
 * @param headers   header
 */
export const post = ({url, data, msg = '', headers}) =>
  axios
    .post (url, data, headers)
    .then (res => {
			// token
			if(env && env === 'development' && res.data.status === 500209){
        window.location.href="http://localhost:3006/#/login";
        window.location.reload()
			}else if(env && env === 'production' && res.data.status === 500209){
        window.location.href="http://47.111.23.117:8081/#/login";
			}else{
				return res.data;
			}
    })
    .catch (msg => {
      message.warn (msg);
    });

/**
 * @description  Post/Get 
 * @param {[request1 , request2]} 
 */
export const postAllRequest = (...requestUrl) => {
  axios.all (requestUrl).then (
    axios.spread ((...result) => {
      result.map (res => {
        if (res && res.status === 200) {
          message.success (res.message);
        } else {
          message.error (res.error);
        }
      });
    })
  );
};
