const tools = {
  /**
    N
    
    
    @params
      str - 
      x   - 
  */
  pointX(str, x = 0) {
    if (!str && str !== 0) {
      return str;
    }
    const temp = Number(str);
    if (temp === 0) {
      return temp.toFixed(x);
    }
    return temp ? temp.toFixed(x) : str;
  },

  /**
     
  */
  trim(str) {
    const reg = /^\s*|\s*$/g;
    return str.replace(reg, "");
  },

  /**
    
    1234561****66*
    2
  */
  addMosaic(str) {
    const s = String(str);
    const lenth = s.length;
    const howmuch = (() => {
      if (s.length <= 2) {
        return s.length;
      }
      const l = s.length - 2;
      if (l <= 6) {
        return l;
      }
      return 6;
    })();
    const start = Math.floor((lenth - howmuch) / 2);
    const ret = s.split("").map((v, i) => {
      if (i >= start && i < start + howmuch) {
        return "*";
      }
      return v;
    });
    return ret.join("");
  },
  /**
   * 
   * 
   * 
   * **/
  checkStr(str) {
    if (str === "") {
      return true;
    }
    const rex = /^[_a-zA-Z0-9]+$/;
    return rex.test(str);
  },
  /**
   * 
   * 
   * 
   * **/
  checkNumber(str) {
    if (!str) {
      return true;
    }
    const rex = /^\d*$/;
    return rex.test(str);
  },
  /**   **/
  checkPhone(str) {
    const rex = /^1[34578]\d{9}$/;
    return rex.test(str);
  },

  /**   **/
  checkEmail(str) {
    const rex = /^[a-zA-Z0-9_-]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/;
    return rex.test(str);
  },
  /**
    
    
  */
  compile(code) {
    let c = String.fromCharCode(code.charCodeAt(0) + code.length);
    for (let i = 1; i < code.length; i++) {
      c += String.fromCharCode(code.charCodeAt(i) + code.charCodeAt(i - 1));
    }
    return c;
  },

  /**
    
    
  */
  uncompile(code) {
    let c = String.fromCharCode(code.charCodeAt(0) - code.length);
    for (let i = 1; i < code.length; i++) {
      c += String.fromCharCode(code.charCodeAt(i) - c.charCodeAt(i - 1));
    }
    return c;
  },

  /**
   * 
   * 0 
   * **/
  clearNull(obj) {
    const temp = {};
    Object.keys(obj).forEach(item => {
      if (obj[item] === 0 || !!obj[item]) {
        temp[item] = obj[item];
      }
    });
    return temp;
  },

  /**
   * QueryString
   */
  getQueryStringByName(name) {
    let result = window.location.search.match(
      new RegExp('[?&]' + name + '=([^&]+)', 'i'),
    );
    if (result == null || result.length < 1) {
      return '';
    }
    return result[1];
  }
};

export default tools;