import React, { useState, useEffect, Fragment } from 'react';
import {Link} from "react-router-dom";
import {
  Table,
  Button,
  Popconfirm,
  Form,
  Modal,
  Pagination
} from 'antd';
import BreadcrumbCustom from '../../BreadcrumbCustom';
import ModalFormContainer from '../../../containers/ModalFormContainer';

// Context
const EditableContext = React.createContext();

// 
const EditableRow = ({ form, index, ...props }) => (
  <EditableContext.Provider value={form}>
    <tr {...props} />
  </EditableContext.Provider>
);

const EditableFormRow = Form.create()(EditableRow);

// 
const EditableCell = props => {

  // Tooltip
  const displaytooltipContent = (e, record, dataIndex) => {
    e.preventDefault();
    // introduction
    if (dataIndex === 'introduction') {
      // setTooltip(record[dataIndex])
    }
  }

  const {
    dataIndex,
    title,
    inputType,
    record,
    index,
    ...restProps
  } = props;

  return (
    <EditableContext.Consumer>
      {form => {
        return (
            <td {...restProps} onClick={e => displaytooltipContent(e, record, dataIndex)}>
              {restProps.children}
            </td>
        );
      }}
    </EditableContext.Consumer>
  );
};

// EditableTable
const EditableTable = props => {

  console.log('11111',props);
  // 
  const [GET_ALL_DATA, DELETE_ALL_DATA, GET_ALL_DATA_BYID, UPDATE ] = props.interfaceUrl;

  // 
  const [data, setData] = useState(null),
        [total, setTotal] = useState(10),
        [visible, setVisible] = useState(false),
        [rowId, setRowId] = useState(undefined)
  // 
  const [page, setPage] = useState(1);

  // 
  const [setSortedInfo] = useState(null);

  // 
  useEffect(() => {
    getData(page)
  }, [page, props._total, props._reload])

  // 
  const getData = page => {
    props.getTableAction(GET_ALL_DATA, page)

    // 
    setData(props._tableData);
    setTotal(props._total);
  };
  // 
  const HandleDelete = id => {
    props.delTableAction(DELETE_ALL_DATA, id);
  };

  // 
  const handleChange = sorter => {
    setSortedInfo(sorter);
  };

  // modleHandleCancel
  const handleCancel = () => {
    setVisible(false);
  }

  // modleHandleOk
  const handleOk = ()=> {
    setVisible(false);
    alert(rowId);
    // props.updateTableAction(UPDATE,rowId,);
  }

  // modleHandleEdit
  const handleEdit = id => {
    setRowId(id);
    setVisible(true);
    console.log('id',id);
  }
  console.log('rowId',rowId);

  // 
  const components = {
    body: {
      row: EditableFormRow, // 
      cell: EditableCell, // 
    },
  };

  // columns()
  const columns = props.columns.map(col => {
    // if (!col.editable) {
    //   return col;
    // }
    return {
      ...col,
      onCell: record => ({
        record,
        dataIndex: col.dataIndex,
        title: col.title
      }),
    };
  });

  const dealOperateFn = () => {
    const columnList = props.columns;
    columnList.forEach(item => {
      if (item.title === '') {
        item.render = (text, record) => {
          return (
            <div>
              <span>
                <EditableContext.Consumer>
                  {form => (
                    <Button type="primary" icon="edit">
                      <Link to="/app/amorous/amorousdata/updata"></Link>
                    </Button>
                  )}
                </EditableContext.Consumer>
                <Popconfirm
                  title="?"
                  onConfirm={() => HandleDelete(record.id)}
                >
                  <a href="javascript:;">
                    <Button icon="delete" type="danger" className="deleteButton">
                      
                    </Button>
                  </a>
                </Popconfirm>
              </span>
            </div>
          );
        };
      }
    });
  };
  dealOperateFn();

  // 
  let crumbsConfig = props.crumbsConfig;

  // 
  return (
    <Fragment>
      <BreadcrumbCustom
        first={crumbsConfig.first}
        second={crumbsConfig.second}
      />
      <EditableContext.Provider value={props.form}>
        <Table
          components={components} // table
          rowClassName={() => 'editable-row'}
          bordered
          dataSource={data}
          columns={columns}
          onChange={handleChange}
          loading={props.loading}
          pagination={false}
        />
        {/*  */}
        <Pagination
          defaultCurrent={1}
          total={total}
          onChange={e => setPage(Number(e))}
          style={{ marginTop: '20px', float: 'right' }}
          showSizeChanger
        />
        <Modal
          width={500}
          title={props.componentName}
          visible={visible}
          okText=""
          cancelText=""
          onCancel={handleCancel}
          onOk={handleOk}
        >
          <ModalFormContainer colums={props.colums} rowId={rowId} getDataById={GET_ALL_DATA_BYID} />
        </Modal>
      </EditableContext.Provider>
    </Fragment>
  );
};

export default EditableTable;

